html, body {
    height: 100%; /* Makes sure the body takes the full height of the screen */
    margin: 0; /* Removes default margin */
  }
  
.site-footer {
    background-color: #919191;
    color: rgb(255, 255, 255);
    padding: 20px;
    text-align: right;
    font-family: 'Arial', sans-serif;
    position: fixed;
    bottom: 0;
    width: 100%;
    flex-direction: column;
  }
  
  .footer-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0px;
    flex-wrap: wrap;
  }
  
  .content {
    flex: 1; /* Make sure content takes remaining space */
    padding: 20px; /* Adjust content padding */
  }
  
  .footer-section {
    flex: 1;
    margin: 10px;
  }

  .footer-section p {
    color: rgb(255, 255, 255);
    margin: 0px 0px 0px 160px;


    font-size: 1.05rem;
    font-weight: 400;
    max-width: 1600px;
    display: flex;
    flex-direction: column;
    align-items: flex-start; 
    margin-top: 5px;
    margin-left: 200px;
    margin-right: 200px;
    text-align: justify; 
  }
  
  .footer-section h4 {
    margin-bottom: 10px;
    font-size: 18px;
  }
  
  .footer-section ul {
    list-style-type: none;
    padding: 0;
  }
  
  .footer-section ul li {
    margin: 5px 0;
  }
  
  .footer-section ul li a {
    color: #ddd;
    text-decoration: none;
  }
  
  .footer-section ul li a:hover {
    text-decoration: underline;
  }
  
  .footer-bottom {
    border-top: 1px solid #21040400;
    margin-top: 20px;
    padding: 10px 0;
  }

  @media screen and (max-width: 768px) {
    .site-footer {
      padding: 10px; /* Less padding for smaller screens */
      text-align: center; /* Center-align text on mobile */
    }
  
    .footer-container {
      flex-direction: column; /* Stack footer sections vertically on mobile */
      align-items: center;
    }
  
    .footer-section {
      width: 100%; /* Make footer sections take full width on mobile */
      text-align: center; /* Center-align text in footer sections */
      margin: 10px 0;
    }
  
    .footer-section p {
      margin: 0 15px; /* Reduce margins for better spacing on mobile */
      font-size: 0.9rem; /* Adjust font size for better readability on small screens */
    }
  
    .footer-bottom {
      font-size: 0.9rem; /* Adjust font size for footer bottom */
    }
  }
  
  @media screen and (max-width: 480px) {
    .footer-section p {
      margin: 0 10px; /* Further reduce margin on very small screens */
      font-size: 0.8rem; /* Reduce font size further for tiny screens */
    }
  
    .footer-bottom {
      font-size: 0.8rem; /* Reduce footer bottom font size */
      padding: 8px 0; /* Less padding for small screens */
    }
  }