.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: rgb(255, 255, 255);
  background-color: #1a1a1a;
  padding: 4px 20px;
  position: fixed; 
  top: 0; 
  left: 0; 
  width: 100%; 
  z-index: 100;
  box-shadow: 0 4px 6px rgba(204, 204, 204, 0.1); 
}
  .navbar .contact-btn {
    background-color: #ffffff; 
    color: #0059ce;
    font-weight: 500;
    border-radius: 2px;
    position: relative;
    left:60px;
    font-size: 0.9em;
    border: none;
    cursor: pointer;
    justify-self: right;
    }
  .navbar .contact-btn:hover {
    background-color: #005fc4;
    color: #ffffff;
  }  
.nav-texts {
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    text-align: center;
  }
  
  h1 {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 60px; 
    color: rgb(0, 70, 136);
  }
    
  h4 {
    font-size: 1.2rem;
    font-weight: 400;
    margin-bottom: 60px; 
    color: rgb(0, 0, 128);
  }
  
  p {
    font-size: 1.05rem;
    color: rgb(7, 2, 66);
    font-weight: 400;
    margin-top: 10px;
    max-width: 1600px;
    display: flex;
    flex-direction: column;
    align-items: flex-start; 
    margin-top: 20px;
    margin-left: 200px;
    margin-right: 200px;
    text-align: justify; 
  }
    
  span {
    font-size: 1.05rem;
    color: rgb(80, 80, 80);
    font-weight: 400;
    margin-top: 10px;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start; 
    margin-top: 20px;
    margin-left: 20px;
    text-align: left; 
    font-style: italic; 
  }
  
  /* Logo */
  .logo {
    font-size: 1.8rem;
    font-weight: 500;
    background: linear-gradient(100deg, #e7e7e7, rgb(0, 104, 202));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  /* Navigation Links */
  .nav-links {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
  }
  .nav-links a {
    text-decoration: none; 
    color: inherit;
  }
  .nav-button {
    background: none;
    border: none;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    padding: 5px 10px;
    transition: all 0.3s ease;
  }
  
  .nav-button:hover {
    color: #0034c2;
  }
  
  /* Active Link */
  .nav-button.active {
    color: #346aff;
    border-bottom: 2px solid #346aff;
  }

  .search-icon {
    cursor: pointer;
    display: flex;
    align-items: center;
    color: rgb(255, 255, 255);
    margin-left: 10px;
  }
  
  .search-bar {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    outline: none;
    background-color: white;
    color: #333;
  }
  h1 {
    animation: textAnimation 1.4s ease-in-out forwards; 
  }
  
  @keyframes textAnimation {
    0% {
      opacity: 0;
      transform: translateY(-20px); 
    }
    50% {
      opacity: 1;
      transform: translateY(0);
    }
    100% {
      opacity: 1; 
      transform: translateY(0);
    }
  }
  .join img {
    animation: slideIn 500ms ease-out forwards;
  }
  
  @keyframes slideIn {
    0% {
      transform: translateX(-20px); 
      opacity: 0; 
    }
    100% {
      transform: translateX(0); 
      opacity: 1;
    }
  }
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #1a1a1a;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.nav-links {
  display: flex;
  gap: 20px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin: 0;
}

.nav-button {
  color: white;
  text-decoration: none;
  font-weight: 500;
}

.nav-button.active {
  border-bottom: 2px solid rgb(0, 102, 255);
}

.search-bar {
  margin-left: 10px;
  padding: 5px;
  border: none;
  border-radius: 4px;
}
/* Hamburger Menu Icon */
.hamburger-icon {
  position: absolute;
  left: 10px; 
  top: 10px; 
  cursor: pointer;
  z-index: 200; 
}
/* Hamburger Menu */
.hamburger-icon {
  display: none;
  cursor: pointer;
}

.nav-links.open {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  background-color: #1a1a1a;
  padding: 10px 0;
  transition: transform 0.3s ease;
}

.nav-links li {
  text-align: center;
  margin: 10px 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .hamburger-icon {
    display: block;
  }
}
/* General Responsive Styles */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }
  
  .navbar .contact-btn {
    margin-left: 0;
    position: static;
    font-size: 0.8em;
    width: 100%;
    text-align: center;
    margin-top: 10px;
  }
  
  .search-icon {
    cursor: pointer;
    display: none;
  }
  .nav-links {
    flex-direction: column;
    gap: 10px;
    width: 100%;
    padding: 10px 0;
  }
  
  .nav-links a {
    font-size: 1rem;
  }

  .search-icon {
    margin-left: 0;
  }
  
  .nav-texts {
    margin-top: 30px; 
  }

  h1 {
    font-size: 1.3rem;
  }

  h4 {
    font-size: 1rem;
  }

  p {
    font-size: 1rem;
    margin: 10px;
  }

  span {
    font-size: 1rem;
    margin: 10px;
  }
  
  .logo {
    font-size: 1.3rem;
    position: relative;
    left:35px;
    bottom:1px;
  }

  .nav-button {
    font-size: 0.9rem;
    padding: 4px;
  }
}

/* Styles for Very Small Screens (Phones) */
@media (max-width: 480px) {
  .navbar {
    padding: 5px;
  }
  
.nav-links.open {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0px;
  left: 0;
  width: 50%;
  height: 100vh;
  background-color: #1a1a1a;
  padding: 50px 40px;
  transition: transform 0.7s ease;
}

  
.search-icon {
  cursor: pointer;
  display: none;
}
  .navbar .contact-btn {
    font-size: 0.75em;
  }

  .nav-links {
    align-items: flex-start;
    gap: 5px;
  }

  .nav-texts img {
    text-align: center;
    margin: 20px 10px;
    width: 350px;
  }
  .nav-texts h4 {
    text-align: center;
    margin: 40px 10px;
  }

  h1 {
    font-size: 1.3rem;
    margin: 58px;
  }

  h4 {
    font-size: 0.9rem;
  }

  p, span {
    font-size: 1.04rem;
    margin: 15px 47px;
  }

}

