/* General Page Styling */
.team-container {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    color: #333;
    background-color: #f9f9f9;
    text-align: center;
    margin: 0 auto;
    padding: 10px;
    line-height: 1.6;
  }
  
.team-page {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    color: #333;
    text-align: center;
    margin: 0 auto;
    padding: 30px;
    max-width: 1100px;
    line-height: 1.6;
  }
  
  /* Professor Section */
  .professor-section {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
    margin-bottom: 50px;
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .profile-image {
    width: 180px;
    height: 180px;
    object-fit: cover;
    border-radius: 50%;
    border: 3px solid #0073e6;
    position: relative;
    animation: scaleIn 1.5s ease-in-out;
    overflow: hidden;
  }
  
  /* Add a circular animation */
  .profile-image::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 3px solid rgba(0, 115, 230, 0.5);
    animation: spin 1.5s linear infinite;
    z-index: -1; /* Keep it behind the image */
  }
  
  /* Keyframes for the scaling animation */
  @keyframes scaleIn {
    0% {
      transform: scale(0.9);
      opacity: 0;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  /* Keyframes for the spinning animation */
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  
  .professor-details {
    text-align: left;
    max-width: 400px;
  }
  
  .professor-details h2 {
    margin: 0;
    font-size: 1.4em;
    color: #333;
  }
  
  .professor-details h3 {
    margin: 5px 0;
    font-weight: 500;
    font-size: 1.1em;
    color: #666;
  }
  
  .professor-details p {
    margin: 8px 0;
    font-size: 1em;
  }
  
  .email-link {
    color: #0073e6;
    text-decoration: none;
    font-weight: bold;
  }
  
  .email-link:hover {
    text-decoration: underline;
  }
  .students-section {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: flex-start;
    padding: 20px;
  }
  
  .student-card {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 250px;
    text-align: center;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .student-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .student-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-bottom: 2px solid #f0f0f0;
  }
  
  .student-info {
    padding: 15px;
  }
  
  .student-name {
    font-size: 18px;
    font-weight: 600;
    color: #333;
    margin: 0;
  }
  .section-title {
    font-size: 19px;
    font-weight: 400;
  }
  .student-role {
    font-size: 14px;
    color: #777;
    margin: 5px 0 0;
  }
  span {
    font-size: 1.05rem;
    color: rgb(80, 80, 80);
    font-weight: 400;
    margin-top: 10px;
    max-width: 1200px;
    display: flex;
    flex-direction: row; 
    align-items: center; 
    margin-top: 20px;
    margin-left: 20px;
    text-align: left;
    font-style: italic; 
  }

/* General adjustments for small screens */
@media (max-width: 768px) {
  .team-container, 
  .team-page {
    padding: 10px;
  }

  .professor-section {
    flex-direction: column;
    gap: 20px; 
    text-align: center; 
  }

  .profile-image {
    width: 150px;
    height: 150px;
  }

  .professor-details {
    text-align: center;
    max-width: 100%; 
  }

  /* Students Section */
  .students-section {
    justify-content: center; 
    padding: 10px; 
    gap: 15px; 
  }

  .student-card {
    width: 90%; 
    margin: 0 auto; 
  }

  .student-image {
    height: 150px; 
  }

  span {
    flex-direction: column; 
    margin: 10px;
    text-align: center;
  }
}

/* Adjustments for very small screens (mobile devices) */
@media (max-width: 480px) {
  .professor-section {
    gap: 10px; 
    padding: 15px;
  }

  .students-section {
    gap: 10px; 
  }

  .student-card {
    width: 100%;
  }

  .profile-image {
    width: 120px;
    height: 120px;
  }

  .student-image {
    height: 300px; 
  }

  span {
    font-size: 0.9rem; 
  }
}
