.page-container {
    margin: 1px; /* Margin from the page edges */
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #dddddd00; /* Optional border for visibility */
    box-sizing: border-box;
  }
  
.image-container {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #cccccc00;     /* Optional border for visibility */
    box-sizing: border-box; 
    margin-bottom: 60px;
  }
  
  .responsive-image {
    width: 100%;
    height: auto;
    border-radius: 8px; /* Optional styling */
  }



/* Media query for mobile devices (screens 768px wide or smaller) */
@media (max-width: 768px) {
    .image-container {
      width: 85%;
    }
}